.header {
	&__wrap {
		display: flex;
		justify-content: space-between;
		align-items: center;
		height: 80px;
	}
	&__logo {
		position: relative;
		width: 150px;
		height: 45px;
		& img {
			position: absolute;
			height: 100%;
			object-fit: contain;
		}
	}
	&__contacts {
		display: none;
	}
}

@media (min-width: 1025px) {
	.header {
		&__contacts {
			display: flex;
			flex-direction: column;
			align-items: end;
			font-weight: 300;
			font-size: 18px;
		}
		&__phone {
			position: relative;
			padding-left: 30px;
			color: #fff;
			margin-bottom: 10px;
			&::before {
				position: absolute;
				content: "";
				width: 17px;
				height: 17px;
				left: 0;
				top: 50%;
				transform: translateY(-50%);
				background-image: url(../img/icons/phone.svg);
				background-repeat: no-repeat;
				background-position: center;
				background-size: contain;
			}
		}
		&__location {
			position: relative;
			padding-left: 30px;
			&::before {
				position: absolute;
				content: "";
				width: 17px;
				height: 17px;
				left: 0;
				top: 50%;
				transform: translateY(-50%);
				background-image: url(../img/icons/location.svg);
				background-repeat: no-repeat;
				background-position: center;
				background-size: contain;
			}
		}
	}
}
