.cases {
	margin-top: 50px;
	margin-bottom: 90px;
	&__wrap {
		position: relative;
	}
	&__tolink {
		position: absolute;
		padding-right: 52px;
		background-image: url(../img/icons/arrow-link.svg);
		background-repeat: no-repeat;
		background-position: right center;
		width: 170px;
		height: 20px;
		bottom: -50px;
		color: $accent;
		font-size: 18px;
		font-weight: 400;
		transition: color 0.3s ease-in-out;
		&:hover {
			color: #fff;
		}
	}
	&__slider {
		position: relative;
		padding-bottom: 40px;
		margin: 20px 0;
	}
}

.swiper {
}
.swiper-wrapper {
}
.swiper-slide {
}
.swiper-pagination {
}
.swiper-button{
	position: absolute;
	width: 65px;
	bottom: 70px;
	right: 1px;
}
.swiper-button-prev,
.swiper-button-next {
	width: 30px;
	height: 30px;
	background-color: #4434349c;
	transition: background-color 0.3s ease-in-out;
	&:hover{
		background-color: #1b1515b6;
	}
}
.swiper-button-prev {
	left: 0;
}
.swiper-button-next {
	right: 0;
}
.slid {
	&__wrap {
		padding: 20px;
		border: solid 1px $accent;
		border-radius: 3px;
	}
	&__descr-item {
		position: relative;
		padding-left: 40px;
		margin-bottom: 23px;
		color: #aeaeaf;
		& img {
			width: 18px;
			height: 18px;
			position: absolute;
			left: 0;
			top: 50%;
			transform: translateY(-50%);
			object-fit: contain;
		}
	}
	&__btn {
		position: relative;
		margin: 30px 0;
		padding-left: 60px;
		color: $accent;
		font-size: 16px;
		background-color: transparent;
		&::before,
		&::after {
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			content: "";
			width: 35px;
			height: 35px;
			border-radius: 50%;
		}
		&::before {
			z-index: 1;
			left: 0;
			background-color: #4b4b4b;
			transition: all 0.3s ease-in-out;
		}
		&::after {
			z-index: 2;
			left: 2px;
			background-image: url(../img/icons/play.svg);
			background-repeat: no-repeat;
			background-position: center center;
		}
		&:hover:before {
			background-color: #343434;
		}
	}
	&__img-block {
		width: 100%;
	}
	&__img {
		position: relative;
		width: 100%;
		min-height: 250px;
		height: 100%;
		overflow: hidden;
		border-radius: 3px;
		& img {
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			object-fit: cover;
			background-position: center;
		}
		&::after {
			position: absolute;
			z-index: 2;
			content: "";
			width: 100%;
			height: 100%;
			background: linear-gradient(-90deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.9) 100%);
		}
	}
}
.swiper-button-next:after,
.swiper-button-prev:after {
	font-size: 15px;
	color: $accent;
}
@media (min-width: 641px) {
	.cases__tolink {
		right: 0;
		top: 5px;
	}
	.slid {
		&__wrap {
			display: flex;
			column-gap: 20px;
			padding-right: 0;
		}
		&__text-block {
			flex: 0 0 250px;
		}
		&__descr {
			margin-bottom: 70px;
		}
		& h5 {
			margin-bottom: 50px;
		}
		&__btn {
			position: absolute;
			bottom: 0;
			left: 20px;
		}
		&__img-block {
			position: relative;
			&::before {
				position: absolute;
				z-index: 3;
				content: "";
				height: 100%;
				width: 1px;
				background-color: $accent;
				left: -10px;
				top: 0;
			}
		}
		&__img {
			min-height: 350px;
		}
	}
}

@media (min-width: 961px) {
	.slid {
		&__wrap {
			column-gap: 30px;
		}
		&__text-block {
			flex: 0 0 300px;
		}
		&__img-block {
			position: relative;
			&::before {
				left: -15px;
			}
		}
	}
}
