// // // https://www.youtube.com/watch?v=E7NJcbie46g

.nav {
	& ul {
		display: flex;
		align-items: center;
	}
	& li:not(:last-child) {
		margin-right: 75px;
	}
	& a {
		color: #fff;
		font-size: 20px;
	}
}

.burger {
	position: relative;
	z-index: 101;
	width: 30px;
	height: 20px;
	display: none;
	border: none;
	padding: 0;
	background-color: transparent;
	cursor: pointer;
	&__line {
		position: absolute;
		height: 2px;
		width: 100%;
		left: 0;
		top: 50%;
		transform: translateY(-50%);
		background-color: $accent;
		transition: opacity 0.3s ease-in-out;
	}
	&::before,
	&::after {
		content: "";
		position: absolute;
		left: 0;
		width: 100%;
		height: 2px;
		background-color: $accent;
		transition: transform 0.3s ease-in-out, top 0.3s ease-in-out, bottom 0.3s ease-in-out;
	}
	&::before {
		top: 0;
	}
	&::after {
		bottom: 0;
	}
}

.burger--active .burger__line {
	opacity: 0;
}

.burger--active::before {
	top: 50%;
	transform: rotate(45deg);
}

.burger--active::after {
	bottom: auto;
	top: 50%;
	transform: rotate(-45deg);
}

.stop--scroll {
	overflow: hidden;
}

@media (max-width: 1024px) {
	.nav {
		position: fixed;
		z-index: 100;
		left: 0;
		top: 0;
		width: 100%;
		height: 100vh;
		overflow-y: auto;
		padding: 90px 30px 30px 30px;
		background-color: rgba(0, 0, 0, 0.68);
		transform: translateX(-100%);
		transition: transform 0.6s ease-in-out;
		& ul {
			flex-direction: column;
			align-items: flex-end;
		}
		& li {
			margin-bottom: 30px;
			margin-right: 20px;
		}
		& li:not(:last-child) {
			margin-right: 20px;
		}
	}
	.nav--visible {
		transform: none;
	}
	.burger {
		display: block;
	}
}
