.footer {
	padding: 40px 0px;
	background-color: #1c1a20;
	&__wrap {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		row-gap: 40px;
	}
	&__logo {
		position: relative;
		width: 90px;
		height: 60px;
		overflow: hidden;
		& img {
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			object-fit: contain;
		}
	}
	&__link {
		flex: 0 0 100%;
		display: flex;
		justify-content: space-between;
		align-items: center;
	}
	&__social {
		display: flex;
		column-gap: 20px;
		padding: 5px;
	}

	&__social-link {
		display: block;
		position: relative;
		height: 18px;
	}
	&__social-img {
		top: 0;
		left: 0;
		transition: scale 0.3s ease-in-out;
		width: 100%;
		&:hover {
			scale: 1.2;
		}
	}
}

.footer {
	&__services {
		font-weight: 600;
		font-size: 16px;
		line-height: 20px;
		text-transform: uppercase;
		& li {
			text-transform: none;
			margin-top: 10px;
			font-weight: 400;
			font-size: 14px;
			line-height: 17px;
			color: rgba(255, 255, 255, 0.7);
			transition: 0.5s;
			max-width: 220px;
		}
	}
}

.footer__menu {
	& li:not(:first-child) {
		margin-top: 15px;
	}
	& a {
		display: block;
		font-weight: 600;
		font-size: 16px;
		line-height: 20px;
		color: #ffffff;
		transition: 0.3s ease-in-out;
	}
}

.footer__contacts {
	display: flex;
	flex-direction: column;
	row-gap: 20px;
	& a,
	.footer__location {
		color: #ffffff;
		position: relative;
		display: block;
		padding-left: 30px;
		transition: color 0.3s ease-in-out;
		&:hover {
			color: $accent;
		}
	}
	& img {
		position: absolute;
		left: 0;
		top: 50%;
		transform: translateY(-50%);
	}
}

.footer__location {
	line-height: 22px;
	max-width: 300px;
	& img {
		width: 15px;
		height: 20px;
	}
}

@media (min-width: 641px) {
	.footer__wrap {
		flex-direction: row;
		flex-wrap: wrap;
	}

	.footer__link {
		flex-direction: column;
		justify-content: start;
		align-items: start;
		flex: 1 0 50%;
	}
	.footer__services,
	.footer__menu,
	.footer__contacts {
		flex: 1 0 50%;
	}
}

@media (min-width: 961px) {
	.footer__wrap {
		flex-direction: row;
		flex-wrap: nowrap;
		column-gap: 20px;
	}
	.footer__link,
	.footer__services,
	.footer__menu,
	.footer__contacts {
		flex: 0 1 25%;
	}
}
