.intro {
	&__video {
		position: absolute;
		z-index: -1;
		top: 0;
		height: 650px;
		width: 100%;
		overflow: hidden;
		&::before,
		&::after {
			position: absolute;
			z-index: 1;
			content: "";
			width: 100%;
			height: 200px;
			background-color: #fff;
		}

		&::before {
			background: linear-gradient(180deg, #121115 0%, rgba(196, 196, 196, 0) 100%);
			top: 0;
		}
		&::after {
			bottom: 0;
			left: 0;
			background: linear-gradient(0deg, #121115 0%, rgba(196, 196, 196, 0) 100%);
		}
		& video {
			height: 100%;
			width: 100%;
			object-fit: cover;
			background-position: center;
		}
	}

	&__wrap {
		min-height: 400px;
	}
	& h1 {
		margin-top: 200px;
		font-size: 27px;
		font-weight: 500;
		letter-spacing: 7px;
		line-height: 35px;
		text-transform: uppercase;
	}
	&__params-item {
		max-width: 250px;
		margin: 20px 0;
		font-size: 16px;
		font-weight: 300;
		line-height: 20px;
		color: rgba(255, 255, 255, 0.7);
		padding-left: 16px;
		position: relative;
		border-left: solid 2px $accent;
	}
	&__btn {
		width: 100%;
		max-width: 500px;
		margin-top: 30px;
		padding: 15px;
		border: 2px solid $accent;
		border-radius: 6px;
		background-color: transparent;
		color: $white;
		font-size: 16px;
		font-weight: 400;
		line-height: 25px;
		transition: background-color ease-in-out 0.3s;
		&:hover {
			background-color: $accent;
		}
	}
}

@media (min-width: 641px) {
	.intro {
		& h1 {
			font-size: 42px;
			line-height: 55px;
			margin-top: 250px;
		}
		&__params {
			display: flex;
		}
		&__params-item {
			margin-right: 25px;
		}
	}
}

@media (min-width: 1025px) {
	.intro {
		h1 {
			font-size: 60px;
			line-height: 65px;
		}
	}
}

