// .services .services .services .services .services .services .services .services
.services {
	&__list {
		display: grid;
		grid-template-columns: 1fr 1fr 1fr;
		grid-template-rows: 1fr 1fr 1fr 1fr 1fr;
		gap: 30px 30px;
		grid-template-areas:
			"a a a"
			"b b b"
			"c c c"
			"d d d"
			"e e e";
	}

	&__item {
		position: relative;
		width: 100%;
		min-height: 300px;
		border-radius: 10px;
		overflow: hidden;
	}

	&__link {
		display: block;
		width: 100%;
		height: 100%;
		color: $white;
	}
	&__img {
		width: 100%;
		height: 100%;
		& img {
			filter: brightness(40%);
			position: absolute;
			width: 100%;
			height: 100%;
			object-fit: cover;
			transition: all 0.3s ease-in-out;
		}
	}
	& h3 {
		position: absolute;
		z-index: 1;
		max-width: 155px;
		left: 30px;
		top: 30px;
		font-size: 18px;
		letter-spacing: 2px;
		line-height: 22px;
		font-weight: 400;
		text-transform: uppercase;
	}
}

.a {
	grid-area: a;
}
.b {
	grid-area: b;
}
.c {
	grid-area: c;
}
.d {
	grid-area: d;
}
.e {
	grid-area: e;
}

.services__link:hover .services__img img {
	scale: 1.1;
	filter: brightness(30%);
}

@media (min-width: 961px) {
	.services__list {
		display: grid;
		grid-template-columns: 1fr 1fr 1fr;
		grid-template-rows: 1fr 1fr 1fr;
		gap: 30px 30px;
		grid-auto-flow: row;
		grid-template-areas:
			"a b b"
			"a c c"
			"d d e";
	}
	.a {
		grid-area: a;
	}
	.b {
		grid-area: b;
	}
	.c {
		grid-area: c;
	}
	.d {
		grid-area: d;
	}
	.e {
		grid-area: e;
	}
}
