// .team .team .team .team .team .team .team .team .team .team .team .team

.team {
	padding: 40px 0 60px 0;
	background-color: #1c1a20;
	&__advantages {
		margin-bottom: 30px;
		& li {
			position: relative;
			padding-left: 40px;
			margin-bottom: 23px;
			color: #aeaeaf;
			& img {
				width: 18px;
				height: 18px;
				position: absolute;
				left: 0;
				top: 50%;
				transform: translateY(-50%);
				object-fit: contain;
			}
		}
	}
	&__dignity {
		margin-bottom: 20px;
		font-size: 16px;
		font-weight: 600;
		line-height: 25px;
	}
	&__dignity-list {
		display: flex;
		flex-wrap: wrap;
		gap: 20px;
		color: #aeaeaf;
		font-size: 16px;
		font-weight: 300;
		& li {
			flex: 0 0 100%;
		}
	}
}

@media (min-width: 641px) {
	.team {
		&__text {
			display: flex;
			gap: 50px;
		}
		&__advantages {
			flex: 0 0 200px;
		}
	}
	.team__dignity-list {
		gap: 30px;
	}
}

@media (min-width: 961px) {
	.team {
		&__advantages {
			flex: 0 0 300px;
		}
	}
	.team__dignity-list {
		& li {
			flex: 0 0 calc(50% - 20px);
		}
	}
}
