// .hardware .hardware .hardware .hardware .hardware .hardware .hardware

.hardware {
	&__wrap {
		padding-top: 50px;
		padding-bottom: 50px;
	}

	&__item {
		position: relative;
		&:not(:last-child) {
			padding-bottom: 40px;
			&::after {
				position: absolute;
				content: "";
				width: 100%;
				height: 1px;
				background-color: $accent;
				bottom: 20px;
				left: 0;
			}
		}
	}
	& h5 {
		margin-bottom: 25px;
	}
	& p {
		color: #aeaeaf;
		text-align: justify;
		line-height: 25px;
		&:not(:last-child) {
			margin-bottom: 20px;
		}
	}
	&__img {
		margin: 20px 0;
		position: relative;
		width: 100%;
		height: 250px;
		overflow: hidden;
		border-radius: 3px;
		@media (min-width: 641px) {
			height: 350px;
		}
		& img {
			position: absolute;
			width: 100%;
			height: 100%;
			left: 0;
			top: 0;
			object-fit: cover;
		}
	}
}

@media (min-width: 961px) {
	.hardware {
		&__list {
			& li:nth-child(even) {
				flex-direction: row-reverse;
			}
		}
		.hardware__item {
			position: relative;
			display: flex;
			column-gap: 40px;
			&:not(:last-child) {
				padding-bottom: 40px;
				&::after {
					position: absolute;
					content: "";
					width: 100%;
					height: 1px;
					background-color: $accent;
					bottom: 20px;
					left: 0;
				}
			}
		}
		&__text-block {
			padding-top: 18px;
		}
		&__img {
			flex: 0 0 50%;
			height: auto;
			aspect-ratio: 16/9;
		}
	}
}
