//  .feedback .feedback .feedback .feedback .feedback .feedback .feedback
.feedback {
	&__wrap {
		padding: 50px 15px;
		background-color: #1c1a20;
	}
	&__title {
		& span {
			color: #aeaeaf;
			padding-top: 15px;
			text-transform: none;
			display: block;
			font-size: 16px;
			font-weight: 300;
			line-height: 18px;
		}
	}
}

.form {
	&__label {
		width: 100%;
		display: block;
		margin-bottom: 20px;
	}
	&__button {
		display: block;
		background-color: transparent;
		color: #fff;
		padding: 10px 20px;
		border: $accent solid 1px;
		border-radius: 3px;
		width: 100%;
		max-width: 300px;
		margin: 0 auto;
		transition: background-color 0.3s ease-in-out;
		&:hover {
			background-color: $accent;
		}
	}
}

.input {
	width: 100%;
	padding: 10px;
	background-color: transparent;
	border-bottom: #343434 solid 1px;
	color: #fff;
	transition: all 0.3s ease-in-out;
	&:hover {
		border-bottom: $accent solid 1px;
	}
}
.textarea {
	width: 100%;
	padding: 10px;
	background-color: transparent;
	border: #343434 solid 1px;
	transition: all 0.3s ease-in-out;
	border-radius: 3px;
	color: #fff;
	&:hover {
		border: $accent solid 1px;
	}
}

