// .question .question .question .question .question .question .question

.question {
	&__wrap {
		padding: 50px 0;
	}
	& h5 {
		font-size: 28px;
		font-weight: 600;
	}
}
.accordion__list {
	max-width: 600px;
	& li:last-child {
		&::after {
			position: absolute;
			content: "";
			width: 100%;
			height: 1px;
			bottom: 0px;
			left: 0px;
			background-color: $accent;
		}
	}
}
.accordion {
	padding: 20px 0;
	position: relative;
	&::before {
		position: absolute;
		content: "";
		width: 100%;
		height: 1px;
		top: 0px;
		left: 0px;
		background-color: $accent;
	}
}
.accordion__control {
	width: 100%;
	background-color: transparent;
	cursor: pointer;
}

.accordion__content {
	max-height: 0;
	opacity: 0;
	overflow: hidden;
	will-change: max-height;
	transition: all 0.3s ease-out;
	box-sizing: content-box;
	font-size: 16px;
	line-height: 22px;
	letter-spacing: 1px;
	color: rgba(255, 255, 255, 0.5);
	text-align: justify;
	& p:nth-child(2) {
		padding-top: 20px;
	}
}

.accordion__title {
	text-align: left;
	color: #fff;
	font-size: 18px;
	font-weight: 400;
	letter-spacing: 3px;
	text-transform: uppercase;
}

.accordion__icon {
	transition: transform 0.3s ease-out;
}

.open .accordion__icon {
	transform: rotate(45deg);
}

.open .accordion__content {
	margin-top: 15px;
	opacity: 1;
}

.accordion__control {
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.accordion__control::-webkit-details-marker {
	display: none;
}

.accordion__icon {
	position: relative;
	display: inline-block;
	width: 30px;
	height: 30px;
	border-radius: 5px;
}

.accordion__icon::before {
	content: "";
	position: absolute;
	left: 50%;
	top: 50%;
	width: 20px;
	height: 1px;
	background-color: $accent;
	transform: translate(-50%, -50%);
}

.accordion__icon::after {
	content: "";
	position: absolute;
	left: 50%;
	top: 50%;
	width: 1px;
	height: 20px;
	background-color: $accent;
	transform: translate(-50%, -50%);
}

